import React, { useState } from 'react';
import Notification from '../components/ui/Notification';

const NotificationContext = React.createContext();
const NotificationConsumer = NotificationContext.Consumer;

function NotificationProvider({ children }) {
  const [isVisible, setVisible] = useState(false);
  const [data, setData] = useState({ message: '', type: 'info' });

  const hide = () => setVisible(false);
  const show = (msg, type) => {
    setData({
      message: msg,
      type: type || data.type
    });
    setVisible(true);
  };

  return (
    <NotificationContext.Provider
      value={{
        isVisible,
        hide,
        show,
        message: data.message
      }}
    >
      <Notification isVisible={isVisible} hide={hide} message={data.message} type={data.type} />
      {children}
    </NotificationContext.Provider>
  );
}

export { NotificationContext, NotificationProvider, NotificationConsumer };
