import React, { useContext, useEffect } from 'react';
import TextField from '@material-ui/core/TextField';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import { Formik } from 'formik';
import * as Yup from 'yup';
import { Redirect } from 'react-router-dom';
import ProgressButton from '../ui/ProgressButton';
import logo from '../../assets/images/logo.png';
import { AuthContext } from '../../context/AuthContext';
import useAPI from '../../hook/useAPI';

function Login() {
  const { setData, isAuth } = useContext(AuthContext);
  const { login } = useAPI();

  const useStyles = makeStyles(theme => ({
    wrapper: {
      minHeight: '100vh',
      backgroundColor: theme.palette.primary.main
    },
    paper: {
      marginTop: theme.spacing(8),
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center'
    },
    form: {
      marginTop: theme.spacing(1)
    },
    submit: {
      margin: theme.spacing(3, 0, 2)
    },
    cardcontent: {
      padding: theme.spacing(3)
    },
    media: {
      height: 140
    }
  }));

  const classes = useStyles();

  const handleLogin = (values, { setSubmitting, setErrors }) => {
    login(values)
      .then(res => {
        const { disabled } = res;
        if (disabled) {
          setErrors("L'utente è disabilitato");
        } else {
          setData({ ...values, ...res });
        }
      })
      .catch(e => setErrors(e.error || e.message))
      .finally(() => setSubmitting(false));
  };

  return isAuth ? (
    <Redirect
      to={{
        pathname: '/dashboard'
      }}
    />
  ) : (
    <>
      <Formik
        initialValues={{ inceptiumid: '', user: '', password: '' }}
        onSubmit={handleLogin}
        validationSchema={Yup.object().shape({
          inceptiumid: Yup.string().required('InceptiumID è obbligatorio'),
          user: Yup.string().required('Nome utente è obbligatorio'),
          password: Yup.string().required('Password è obbligatorio')
        })}
      >
        {props => {
          const {
            values,
            touched,
            errors,
            dirty,
            isSubmitting,
            handleChange,
            handleBlur,
            handleSubmit
          } = props;
          return (
            <Grid
              container
              spacing={0}
              alignItems="center"
              justify="center"
              className={classes.wrapper}
            >
              <Grid item xs={6} sm={3}>
                <Card>
                  <CardContent className={classes.cardcontent}>
                    <CardMedia className={classes.media} image={logo} />
                    <form className={classes.form} onSubmit={handleSubmit}>
                      <TextField
                        variant="outlined"
                        margin="normal"
                        fullWidth
                        id="inceptiumid"
                        label="Inceptium ID"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.inceptiumid}
                        helperText={errors.inceptiumid && touched.inceptiumid && errors.inceptiumid}
                        error={errors.inceptiumid && touched.inceptiumid}
                        disabled={isSubmitting}
                      />
                      <TextField
                        variant="outlined"
                        margin="normal"
                        fullWidth
                        id="user"
                        label="Nome Utente"
                        autoComplete="user"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.user}
                        helperText={errors.user && touched.user && errors.user}
                        error={errors.user && touched.user}
                        disabled={isSubmitting}
                      />
                      <TextField
                        variant="outlined"
                        margin="normal"
                        fullWidth
                        label="Password"
                        type="password"
                        id="password"
                        autoComplete="current-password"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.password}
                        helperText={errors.password && touched.password && errors.password}
                        error={errors.password && touched.password}
                        disabled={isSubmitting}
                      />
                      <div className={classes.submit}>
                        <ProgressButton
                          type="submit"
                          fullWidth
                          variant="contained"
                          color="primary"
                          disabled={isSubmitting}
                          isLoading={isSubmitting}
                        >
                          Login
                        </ProgressButton>
                      </div>
                    </form>
                  </CardContent>
                </Card>
              </Grid>
            </Grid>
          );
        }}
      </Formik>
    </>
  );
}

export default Login;
