import React, { useState, useEffect } from 'react';
import ability from '../config/ability';

const AuthContext = React.createContext();
const AuthConsumer = AuthContext.Consumer;

function AuthProvider({ children }) {
  const getSession = () => localStorage.getItem('session');
  const setData = ({ session, inceptiumid, user, password, type, disabled }) => {
    localStorage.setItem('session', session);
    localStorage.setItem('inceptiumid', inceptiumid);
    localStorage.setItem('user', user);
    localStorage.setItem('password', password);
    localStorage.setItem('type', type);

    // Set Auth State
    setAuth(true);
  };
  const getData = () => {
    return {
      inceptiumid: localStorage.getItem('inceptiumid'),
      user: localStorage.getItem('user'),
      password: localStorage.getItem('password'),
      session: localStorage.getItem('session')
    };
  };
  const [isAuth, setAuth] = useState(getSession() !== null);

  const logout = async () => {
    // TODO: Make API CALL
    localStorage.clear();
    setAuth(false);
  };

  useEffect(() => {
    console.log('Updating user abilities');
    let rules = [];
    if (isAuth) {
      if (localStorage.getItem('type') === 'admins') {
        rules = [{ subject: 'User', actions: 'manage' }];
      }

      ability.update(rules);
    }
  }, [isAuth]);

  return (
    <AuthContext.Provider
      value={{
        isAuth,
        setData,
        getData,
        logout
      }}
    >
      {children}
    </AuthContext.Provider>
  );
}

export { AuthContext, AuthProvider, AuthConsumer };
