import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import ConnectedIcon from '@material-ui/icons/Cloud';
import DisconnectedIcon from '@material-ui/icons/CloudOff';
import Tooltip from '@material-ui/core/Tooltip';
import TopBar from '../app/child-component/TopBar';
import NavDrawer from '../app/child-component/NavDrawer';
import Table from '../ui/Table';
import AlertDialog from '../ui/AlertDialog';
import useAPI from '../../hook/useAPI';

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex'
  },
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    height: '100vh',
    overflow: 'auto'
  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4)
  },
  paper: {
    padding: theme.spacing(2),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
    height: 240
  }
}));

export default function UserList(props) {
  const classes = useStyles();
  const { getUsers } = useAPI();
  const [users, setUsers] = useState([]);
  const [isOpen, setOpen] = useState(false);

  const [user, setUser] = useState();

  useEffect(() => {
    getUsers()
      .then(data => setUsers(data))
      .catch(e => console.error(e));
  }, []);

  useEffect(() => {
    console.log(users);
  }, [users]);

  const deleteUser = username => setUsers(users.filter(user => user.username !== username));

  return (
    <div className={classes.root}>
      <TopBar />
      <NavDrawer />
      <main className={classes.content}>
        <div className={classes.appBarSpacer} />
        <Container maxWidth="lg" className={classes.container}>
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <Table
                title="Lista utenti"
                columns={[
                  { title: 'Utente', field: 'username' },
                  { title: 'Nome e cognome', field: 'fullName' },
                  { title: 'E-mail', field: 'email' },
                  { title: 'Tipologia', field: 'type' },
                  { title: 'Descrizione', field: 'description' },
                  {
                    title: 'Stato',
                    field: 'isOnline',
                    render: rowData => (
                      <div>
                        {rowData.isOnline ? (
                          <Tooltip title="Connesso">
                            <ConnectedIcon />
                          </Tooltip>
                        ) : (
                          <Tooltip title="Disconnesso">
                            <DisconnectedIcon />
                          </Tooltip>
                        )}
                      </div>
                    )
                  }
                ]}
                data={users}
                actions={[
                  {
                    icon: 'add_box',
                    tooltip: 'Aggiungi utente',
                    isFreeAction: true,
                    onClick: () => props.history.push('/user/add')
                  },
                  {
                    icon: 'edit',
                    tooltip: 'Modifica',
                    onClick: (_, { id }) => props.history.push(`/user/edit/${id}`)
                  },
                  rowData => ({
                    icon: 'delete',
                    tooltip: 'Elimina',
                    onClick: (_, { username }) => {
                      setOpen(true);
                      setUser(username);
                    }
                  })
                ]}
              />
              <AlertDialog
                isOpen={isOpen}
                handlePrimaryClose={() => setOpen(false)}
                handleSecondaryClose={() => {
                  setOpen(false);
                  deleteUser(user);
                }}
                handleClose={() => setOpen(false)}
                title="Eliminazione utente"
                description={`Sei sicuro di voler eliminare l'utente ${user}?`}
              />
            </Grid>
          </Grid>
        </Container>
      </main>
    </div>
  );
}
