import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import TopBar from '../app/child-component/TopBar';
import NavDrawer from '../app/child-component/NavDrawer';
import AppCard from './child-component/AppCard';
import AppIcon from '../../assets/images/app_icon.png';
import useAPI from '../../hook/useAPI';

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex'
  },
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    height: '100vh',
    overflow: 'auto'
  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4)
  },
  appSeparator: {
    minHeight: theme.spacing(10)
  }
}));

export default function Dashboard() {
  const classes = useStyles();
  const { getApps } = useAPI();
  const [apps, setApps] = useState([]);

  useEffect(() => {
    getApps()
      .then(res => {
        setApps(res);
      })
      .catch(e => console.error(e));
    // TODO: Show Error loading
  }, []);

  return (
    <div className={classes.root}>
      <TopBar title="Dashboard" />
      <NavDrawer />
      <main className={classes.content}>
        <div className={classes.appBarSpacer} />
        <div className={classes.appSeparator} />
        <Container maxWidth="lg" className={classes.container}>
          <Grid container spacing={3}>
            {apps.map(app => (
              <AppCard
                key={app.class_path_name}
                id={app.class_path_name}
                title={app.app_name}
                description={`${app.developer} - ${app.contact_mail}`}
                image={AppIcon}
              />
            ))}
          </Grid>
        </Container>
      </main>
    </div>
  );
}
