import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import TopBar from '../app/child-component/TopBar';
import NavDrawer from '../app/child-component/NavDrawer';
import MenuButton from '../menu/MenuButton';
import useAPI from '../../hook/useAPI';

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex'
  },
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    height: '100vh',
    overflow: 'auto'
  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4)
  }
}));

export default function Application({ match }) {
  const classes = useStyles();
  const { startApp } = useAPI();
  const [app, setApp] = useState({ title: 'Caricamento...', menu: [] });

  const { id } = match.params;

  console.log('Loading app', id);
  useEffect(() => {
    startApp(id)
      .then(data => {
        console.log('Application loaded', data);
        setApp({
          title: data.app_name,
          menu: data.menu
        });
      })
      .catch(e => console.log('Failed to start app', e));
  }, []);

  return (
    <div className={classes.root}>
      <TopBar title={app.title}>
        {app.menu.map(menu => (
          <MenuButton
            key={menu.id_menu}
            id={menu.classToCall}
            label={menu.value}
            items={menu.children}
          />
        ))}
      </TopBar>
      <NavDrawer />
      <main className={classes.content}>
        <div className={classes.appBarSpacer} />
        <Container maxWidth="lg" className={classes.container}>
          {app.title}
        </Container>
      </main>
    </div>
  );
}
