import React from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import { BrowserRouter as Router, Route, Switch, Redirect } from 'react-router-dom';
import PrivateRoute from './child-component/PrivateRoute';
import NotFound from './child-component/NotFound';
import Login from '../login';
import { AuthProvider } from '../../context/AuthContext';
import { NavDrawerProvider } from '../../context/NavDrawerContext';
import { AbilityProvider } from '../../context/AbilityContext';
import { ProgressBarProvider } from '../../context/ProgressBarContext';
import ability from '../../config/ability';
import Dashboard from '../dashboard';
import UserList from '../users/UserList';
import UserAddEdit from '../users/UserAddEdit';
import Application from '../application';
import { NotificationProvider } from '../../context/NotificationContext';

const theme = createMuiTheme({
  palette: {
    primary: {
      light: '#FFE0B2',
      main: '#FF9800',
      dark: '#F57C00',
      contrastText: '#FFFFFF'
    },
    secondary: {
      main: '#FFC107'
    }
  }
});

function App() {
  return (
    <MuiThemeProvider theme={theme}>
      <Router basename="/portaledef">
        <React.Fragment>
          <CssBaseline /> {/* MaterialUI css adjustments. Must be here. */}
          <ProgressBarProvider>
            <NotificationProvider>
              <AuthProvider>
                <AbilityProvider value={ability}>
                  <NavDrawerProvider>
                    <Switch>
                      {/* Redirect all requests to / to dashboard */}
                      <Redirect exact from="/" to="/dashboard" />

                      <Route path="/login" component={Login} />
                      <PrivateRoute path="/dashboard" component={Dashboard} />
                      <PrivateRoute path="/user/list" component={UserList} />
                      <PrivateRoute path="/user/add" component={UserAddEdit} />
                      <PrivateRoute path="/app/:id" component={Application} />

                      {/* Render 404 component if no route matches */}
                      <Route component={NotFound} />
                    </Switch>
                  </NavDrawerProvider>
                </AbilityProvider>
              </AuthProvider>
            </NotificationProvider>
          </ProgressBarProvider>
        </React.Fragment>
      </Router>
    </MuiThemeProvider>
  );
}

export default App;
