import React from 'react';
import { Link } from 'react-router-dom';
import MenuItem from '@material-ui/core/MenuItem';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import SupervisorAccount from '@material-ui/icons/SupervisorAccount';
import Menu from '@material-ui/core/Menu';
import ExitToApp from '@material-ui/icons/ExitToApp';
import { makeStyles } from '@material-ui/core/styles';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import Tooltip from '@material-ui/core/Tooltip';
import MoreIcon from '@material-ui/icons/MoreVert';
import Divider from '@material-ui/core/Divider';
import { AuthContext, AuthConsumer } from '../../context/AuthContext';
import { Can } from '../../context/AbilityContext';

export default function UserMenu() {
  const menuId = 'user-menu';

  const [anchorEl, setAnchorEl] = React.useState(null);
  const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = React.useState(null);

  const isMenuOpen = Boolean(anchorEl);
  const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);

  const { logout } = React.useContext(AuthContext);

  const handleMenuOpen = event => setAnchorEl(event.currentTarget);
  const handleMenuClose = () => {
    setAnchorEl(null);
    setMobileMoreAnchorEl(null);
  };
  const handleLogout = () => {
    handleMenuClose();
    logout();
  };

  const useStyles = makeStyles(theme => ({
    grow: {
      flexGrow: 1
    },
    title: {
      display: 'none',
      [theme.breakpoints.up('sm')]: {
        display: 'block'
      }
    },
    sectionDesktop: {
      display: 'none',
      [theme.breakpoints.up('md')]: {
        display: 'flex'
      }
    },
    sectionMobile: {
      display: 'flex',
      [theme.breakpoints.up('md')]: {
        display: 'none'
      }
    },
    paper: {
      width: theme.spacing(30)
    }
  }));
  const classes = useStyles();

  const renderMenu = (
    <Menu
      anchorEl={anchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      id={menuId}
      keepMounted
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={isMenuOpen}
      onClose={handleMenuClose}
      PaperProps={{ className: classes.paper }}
    >
      <MenuItem disabled>
        <AuthConsumer>
          {({ getData }) => {
            const { user, inceptiumid } = getData();
            return (
              <Typography>
                {inceptiumid} - {user}
              </Typography>
            );
          }}
        </AuthConsumer>
      </MenuItem>
      <Divider />
      <Can I="manage" a="User">
        {() => (
          <MenuItem component={Link} to="/user/list" onClick={handleMenuClose}>
            <ListItemIcon>
              <SupervisorAccount />
            </ListItemIcon>
            <Typography variant="inherit">Gestione utenti</Typography>
          </MenuItem>
        )}
      </Can>
      <MenuItem onClick={handleLogout}>
        <ListItemIcon>
          <ExitToApp />
        </ListItemIcon>
        <Typography variant="inherit">Logout</Typography>
      </MenuItem>
    </Menu>
  );

  return (
    <div>
      <div className={classes.grow} />
      <div className={classes.sectionDesktop}>
        <Tooltip title="Menu utente" enterDelay={200}>
          <IconButton
            edge="end"
            aria-label="user menu"
            aria-controls={menuId}
            aria-haspopup="true"
            onClick={handleMenuOpen}
            color="inherit"
          >
            <MoreIcon />
          </IconButton>
        </Tooltip>
      </div>
      {renderMenu}
    </div>
  );
}
