import React from 'react';
import TextField from '@material-ui/core/TextField';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import { makeStyles } from '@material-ui/core/styles';
import { Formik } from 'formik';
import * as Yup from 'yup';
import Select from '@material-ui/core/Select';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Typography from '@material-ui/core/Typography';
import NavDrawer from '../app/child-component/NavDrawer';
import TopBar from '../app/child-component/TopBar';
import ProgressButton from '../ui/ProgressButton';

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex'
  },
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    height: '100vh',
    overflow: 'auto'
  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4)
  },
  paper: {
    padding: theme.spacing(2),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
    height: 240
  },
  form: {
    marginTop: theme.spacing(1)
  },
  formControl: {
    margin: theme.spacing(1)
  },
  submitContainer: {
    display: 'flex',
    justifyContent: 'flex-end'
  }
}));

export default function UserAddEdit() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <TopBar title="Gestione utenti" />
      <NavDrawer />
      <main className={classes.content}>
        <div className={classes.appBarSpacer} />
        <Container maxWidth="lg" className={classes.container}>
          <Card>
            <CardContent className={classes.cardcontent}>
              <Typography variant="h6" gutterBottom>
                Aggiungi utente
              </Typography>
              <Formik
                initialValues={{ username: '', fullName: '', email: '', type: '', description: '' }}
                onSubmit={() => {}}
                validationSchema={Yup.object().shape({
                  username: Yup.string().required('Il nome utente è obbligatorio'),
                  email: Yup.string().email('Inserire un indirizzo email valido')
                })}
              >
                {props => {
                  const {
                    values,
                    touched,
                    errors,
                    dirty,
                    isSubmitting,
                    handleChange,
                    handleBlur,
                    handleSubmit
                  } = props;
                  return (
                    <form className={classes.form} onSubmit={handleSubmit}>
                      <Grid container spacing={3}>
                        <Grid item xs={12} sm={6}>
                          <TextField
                            variant="outlined"
                            margin="normal"
                            fullWidth
                            id="username"
                            label="Nome utente"
                            onChange={handleChange}
                            onBlur={handleBlur}
                            autoComplete="new-password"
                            inputProps={{
                              form: {
                                autocomplete: 'off'
                              }
                            }}
                            value={values.username}
                            helperText={errors.username && touched.username && errors.username}
                            error={errors.username && touched.username}
                            disabled={isSubmitting}
                          />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                          <FormControl variant="outlined" className={classes.formControl} fullWidth>
                            <InputLabel htmlFor="account-type">Tipologia account</InputLabel>
                            <Select
                              native
                              value="type"
                              onChange={() => {}}
                              input={<OutlinedInput name="Tipologia account" id="account-type" />}
                            >
                              <option value="admins">Amministratore</option>
                              <option value="users">Utente</option>
                            </Select>
                          </FormControl>
                        </Grid>
                        <Grid item xs={12} sm={6}>
                          <TextField
                            variant="outlined"
                            margin="normal"
                            fullWidth
                            label="Password"
                            type="password"
                            id="password"
                            autoComplete="current-password"
                            onChange={handleChange}
                            onBlur={handleBlur}
                            value={values.password}
                            helperText={errors.password && touched.password && errors.password}
                            error={errors.password && touched.password}
                            disabled={isSubmitting}
                          />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                          <TextField
                            variant="outlined"
                            margin="normal"
                            fullWidth
                            label="Conferma Password"
                            type="password"
                            id="passwordConfirm"
                            autoComplete="current-password"
                            onChange={handleChange}
                            onBlur={handleBlur}
                            value={values.password}
                            helperText={errors.password && touched.password && errors.password}
                            error={errors.password && touched.password}
                            disabled={isSubmitting}
                          />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                          <TextField
                            variant="outlined"
                            margin="normal"
                            fullWidth
                            id="email"
                            label="Email"
                            onChange={handleChange}
                            onBlur={handleBlur}
                            inputProps={{
                              form: {
                                autocomplete: 'off'
                              }
                            }}
                            value={values.email}
                            helperText={errors.email && touched.email && errors.email}
                            error={errors.email && touched.email}
                            disabled={isSubmitting}
                          />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                          <TextField
                            variant="outlined"
                            margin="normal"
                            fullWidth
                            id="description"
                            label="Description"
                            onChange={handleChange}
                            onBlur={handleBlur}
                            value={values.description}
                            helperText={
                              errors.description && touched.description && errors.description
                            }
                            error={errors.description && touched.description}
                            disabled={isSubmitting}
                          />
                        </Grid>
                        <Grid item xs={12} sm={12}>
                          <div className={classes.submitContainer}>
                            <ProgressButton
                              type="submit"
                              fullWidth
                              variant="contained"
                              color="primary"
                              disabled={isSubmitting}
                              isLoading={isSubmitting}
                            >
                              Salva
                            </ProgressButton>
                          </div>
                        </Grid>
                      </Grid>
                    </form>
                  );
                }}
              </Formik>
            </CardContent>
          </Card>
        </Container>
      </main>
    </div>
  );
}
