import React, { useState } from 'react';

const ProgressBarContext = React.createContext();
const ProgressBarConsumer = ProgressBarContext.Consumer;

function ProgressBarProvider({ children }) {
  const [isLoading, setLoading] = useState(false);

  return (
    <ProgressBarContext.Provider
      value={{
        isLoading,
        setLoading
      }}
    >
      {children}
    </ProgressBarContext.Provider>
  );
}

export { ProgressBarContext, ProgressBarProvider, ProgressBarConsumer };
