import { useContext, useEffect } from 'react';
import axios from 'axios';
import { AuthContext } from '../context/AuthContext';
import { ProgressBarContext } from '../context/ProgressBarContext';
import useNotification from './useNotification';

const sleep = ms => {
  return new Promise(resolve => setTimeout(resolve, ms));
};

const APIError = function(reason, error) {
  const err = new Error(reason);
  err.error = error;
  return err;
};

const axiosInstance = axios.create({
  baseURL: 'https://www.inceptium.it/inapi/',
  paramsSerializer: params => {
    return Object.keys(params)
      .map(key => `${key}=${params[key]}`)
      .join('::');
  }
});

const useAPI = (showProgress = true) => {
  const { getData, isAuth, logout } = useContext(AuthContext);
  const { setLoading } = useContext(ProgressBarContext);
  const showNotification = useNotification();

  console.log('useAPI()');

  useEffect(() => {
    const reqInterceptor = axiosInstance.interceptors.request.use(
      config => {
        if (isAuth) {
          console.log('Updating request parameters');
          config.params = {
            ...getData(),
            ...config.params
          };
        }
        // Log API requests
        console.log(
          `Making API Request to ${config.baseURL}${config.url} params ${JSON.stringify(
            config.params
          )}`
        );
        if (showProgress) setLoading(true);
        return config;
      },
      err => {
        if (showProgress) setLoading(false);
        showNotification('Errore del server. Riprovare.', 'error');
        return Promise.reject(err);
      }
    );

    // Add a response interceptor
    const resInterceptor = axiosInstance.interceptors.response.use(
      res => {
        if (showProgress) setLoading(false);
        // Log response data
        console.log('Got response data', res);
        if (
          res.status === 200 &&
          typeof res.data === 'string' &&
          res.data.includes('UNAUTHORIZED')
        ) {
          console.log('Got unauthorized 401');
          // Session timed out, redirect to login page
          logout();
        }
        return res;
      },
      err => {
        if (showProgress) setLoading(false);
        showNotification('Errore del server. Riprovare.', 'error');
        // Do something with response error
        return Promise.reject(err);
      }
    );

    return () => {
      console.log('Removing axios interceptors');
      axiosInstance.interceptors.request.eject(reqInterceptor);
      axiosInstance.interceptors.response.eject(resInterceptor);
    };
  }, []);

  const login = async ({ inceptiumid, user, password }) => {
    try {
      const { data } = await axiosInstance.get('get_new_inceptium_session', {
        params: {
          inceptiumid,
          user,
          password
        }
      });

      const regex = /(?<=wsession=).*/;
      const res = regex.exec(data);
      if (!res) throw new Error(`Got invalid response: ${data}`);
      const session = res[0];
      console.log('Session token is', session);

      const { data: userData } = await axiosInstance.get('get_user_by_login', {
        params: {
          user_login: 'admin',
          inceptiumid,
          user,
          password,
          session
        }
      });

      const { user_profile: type } = userData;
      let { disabled } = userData;
      disabled = disabled === 'true';

      if (disabled) {
        return Promise.reject(
          new APIError('Questo utente è disabilitato', {
            user: 'Questo utente è disabilitato'
          })
        );
      }

      console.log(`User is ${type} and disable ${disabled}`);

      return Promise.resolve({
        session,
        disabled,
        type
      });
    } catch (e) {
      if (e.response.status === 401) {
        return Promise.reject(
          new APIError('Controlla le credenziali e riprova', {
            user: 'Controlla le credenziali e riprova',
            password: 'Controlla le credenziali e riprova',
            inceptiumid: 'Controlla le credenziali e riprova'
          })
        );
      }

      return Promise.reject(e);
    }
  };

  const getUsers = async () => {
    console.log('getUsers()');
    try {
      const { data } = await axiosInstance.get('get_users_of_inceptiumid');
      const users = data.map(user => {
        return {
          id: user.id_WebUsers,
          description: user.description,
          isOnline: user.user_connected_status === 'CONNECTED',
          type: user.user_profile,
          email: user.user_email,
          fullName: `${user.user_name} ${user.user_surname}`,
          username: user.user
        };
      });
      return Promise.resolve(users);
    } catch (e) {
      return Promise.reject(e);
    }
  };

  const getApps = async () => {
    console.log('getApps()');
    try {
      const { data } = await axiosInstance.get('get_apps_inceptiumid');
      return Promise.resolve(data);
    } catch (e) {
      return Promise.reject(e);
    }
  };

  const startApp = async classapp => {
    try {
      const { data: taskid } = await axiosInstance.get('load_app', {
        params: {
          classapp
        }
      });

      console.log('App is loading with taskid', taskid);

      while (true) {
        const { data } = await axiosInstance.get('get_task_status', {
          params: {
            taskid
          }
        });

        console.log(data);
        const { currentStatus: taskStatus } = data;
        if (taskStatus === 'TASK_END') {
          console.log('Loading ended succesfully');
          break;
        } else if (taskStatus === 'TASK_RUNNING') {
          await sleep(1000);
        } else {
          throw new Error('Task failed');
        }
      }

      const { data: menuData } = await axiosInstance.get('get_app_dock', {
        params: {
          classapp
        }
      });
      const { menu_list: menu } = menuData;

      const apps = await getApps();
      const details = apps.filter(app => app.class_path_name === classapp)[0];

      return Promise.resolve({
        ...details,
        menu
      });
    } catch (e) {
      console.error(e);
      return Promise.reject(e);
    }
  };

  return { login, startApp, getApps, getUsers };
};

export default useAPI;
