import React from 'react';
import Drawer from '@material-ui/core/Drawer';
import { makeStyles } from '@material-ui/core/styles';
import { Link } from 'react-router-dom';
import Dashboard from '@material-ui/icons/Dashboard';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import { NavDrawerContext } from '../../../context/NavDrawerContext';

function NavDrawer({ children }) {
  const { isOpen, close: handleDrawerClose } = React.useContext(NavDrawerContext);

  const drawerWidthOpened = 240;

  const useStyles = makeStyles(theme => ({
    drawerOpen: {
      width: drawerWidthOpened,
      flexShrink: 0
    },
    drawerClose: {
      width: theme.spacing(7),
      flexShrink: 0
    },
    drawerPaper: {
      position: 'relative',
      whiteSpace: 'nowrap',
      width: drawerWidthOpened,
      flexShrink: 0,
      transition: theme.transitions.create('width', {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.enteringScreen
      })
    },
    drawerPaperClose: {
      overflowX: 'hidden',
      transition: theme.transitions.create('width', {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen
      }),
      width: theme.spacing(7),
      [theme.breakpoints.up('sm')]: {
        width: theme.spacing(7)
      }
    },
    toolbar: theme.mixins.toolbar
  }));

  const classes = useStyles();

  return (
    <Drawer
      variant="permanent"
      className={isOpen ? classes.drawerOpen : classes.drawerClose}
      classes={{
        paper: isOpen ? classes.drawerPaper : classes.drawerPaperClose
      }}
      open={isOpen}
    >
      <div className={classes.toolbar} />
      <Divider />
      <List>
        {/* selected */}
        <ListItem
          button
          onClick={handleDrawerClose}
          component={Link}
          to="/dashboard"
          key="dashboard"
          divider
        >
          <ListItemIcon>
            <Dashboard />
          </ListItemIcon>
          <ListItemText
            secondary="Dashboard"
            secondaryTypographyProps={{ fontWeight: 'fontWeightBold' }}
          />
        </ListItem>
      </List>
      {children}
    </Drawer>
  );
}

export default NavDrawer;
